function sliderMain() {
    new Swiper(".main__slider .mySwiper",{
        slidesPerView: 2,
        spaceBetween: 10,
        pagination: {
            el: ".main__slider .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            360: {
                slidesPerView: 2,
            }
        }
    })
}

function sliderMain2() {
    new Swiper(".main__slider2 .mySwiper",{
        slidesPerView: "auto",
        spaceBetween: 20,
        slideToClickedSlide: 'true',
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            577: {
                spaceBetween: 20,
            }
        }
    })
}

let SlidersArray = []
function sliderWorks() {
    $(".works").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.works[data-slider-id="${index}"] .mySwiper2`, {
            slidesPerView: 2,
            spaceBetween: 10,
            slideToClickedSlide: 'true',
            pagination: {
                el: `.works[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1,
                },
                360: {
                    slidesPerView: 2,
                }
            }
        })
    })
}

function sliderPopular() {
    new Swiper(".popular .mySwiper",{
        slidesPerView: 3,
        spaceBetween: 15,
        pagination: {
            el: ".popular .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
            },
            360: {
                slidesPerView: 3,
            }
        }
    })
}

function sliderArt() {
    new Swiper(".art .mySwiper",{
        slidesPerView: 2,
        spaceBetween: 10,
        pagination: {
            el: ".art .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            360: {
                slidesPerView: 2,
            }
        }
    })
}


$(document).ready(function () {
    sliderMain();
    sliderMain2();
    sliderWorks();
    sliderPopular();
    sliderArt();


    // Select
    $(".custom_select select").niceSelect()

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

})